@import url("https://fonts.googleapis.com/css2?family=Roboto+Slab:wght@400;700;800;900&display=swap");
* {
  box-sizing: border-box; }

body {
  font-family: "Roboto Slab", serif !important; }

a {
  text-decoration: none; }

.container {
  max-width: 1140px;
  width: 100%; }

.container-fluid {
  width: 100%; }

.grid-container-2-fr {
  display: grid;
  grid-template-columns: 100fr 1fr; }

.active-list {
  display: block; }

.inactive-list {
  display: none; }

.navigation__container {
  background-color: #def7ff; }
  .navigation__container__header {
    font-size: 20px;
    line-height: 20px;
    padding: 14px 0;
    text-align: center;
    letter-spacing: normal; }
  .navigation__container__toggler {
    background-color: #def7ff;
    cursor: pointer;
    text-decoration: none; }

.navigation__list {
  background-color: #d9f0f7; }
  .navigation__list li {
    padding: 24px 16px; }

.active,
.bookmarks__collapsible:hover {
  background-color: #555; }

.bookmarks {
  margin-top: 20px; }
  .bookmarks__collapsible {
    background-color: #777;
    color: white;
    cursor: pointer;
    padding: 18px;
    width: 100%;
    border: none;
    text-align: left;
    outline: none;
    font-size: 15px; }
  .bookmarks__content {
    padding: 0 18px;
    display: none;
    overflow: hidden;
    background-color: #f1f1f1; }
    .bookmarks__content__list {
      margin-left: 50px;
      list-style: square; }

.login-twitter {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 500px; }
  .login-twitter__link {
    background: #1d9bf0;
    cursor: pointer;
    padding: 20px;
    border-radius: 25px; }
    .login-twitter__link__heading {
      display: inline; }

@media only screen and (min-width: 768px) {
  .navigation__container {
    padding-left: 15px;
    padding-right: 15px;
    width: 100%;
    margin-right: auto;
    margin-left: auto; }
    .navigation__container__header {
      text-align: start; } }
